
import {React, useState, useEffect} from "react";
import styled from "styled-components";
import axios from "axios";

const Wrapper = styled.div`
height: 100%;
    padding: 16px;
    display: flex;
    flex-direction: column;

    position: sticky;
    top: 10px;
    z-index: 1;
    background-color: white;
`;

const Card = styled.div`
    outline: 0px;
    display: inline-block;
    -webkit-tap-highlight-color: transparent;
    text-decoration: none;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(8, 60, 130, 0.06) 0px 0px 0px 0.05rem, rgba(30, 34, 40, 0.04) 0rem 0rem 1.25rem;
    border-radius: 15px;
    padding: 24px;
    margin-top: 16px;
`;

const StyleInfo = styled.div`
    display: flex;
    flex-direction: row;
    font-weight: 600;
    font-size: 1.25rem;
    letter-spacing: -0.025em;
    justify-content: space-between;
    margin-bottom: 1rem;
`;


function DietUserBodyInfoCard(props) {
    const {id, diet_status, min_nutrient, max_nutrient} = props
    
    const [purchase, setPurchase] = useState([]);
    useEffect(() => {

        axios.get(`${process.env.REACT_APP_API}/api/week-diets/${id}/`)
        .then(res => {
                setPurchase(res.data.Diet_Purchase_info);
            })
            .catch(err => {
                console.log(err);
            })
    }, [id]);
    return (
        <Wrapper>
            <Card>
                <StyleInfo>{diet_status === 1 ? "" : ""}    </StyleInfo>
                <StyleInfo>   </StyleInfo>
                <StyleInfo> </StyleInfo>
                <StyleInfo> : {min_nutrient.kcal} ~ {max_nutrient.kcal}kcal</StyleInfo>
                <StyleInfo> : {min_nutrient.protein} ~ {max_nutrient.protein}g</StyleInfo>
                <StyleInfo> : {min_nutrient.fat} ~ {max_nutrient.fat}g</StyleInfo>
                <StyleInfo> : {min_nutrient.carbs} ~ {max_nutrient.carbs}g</StyleInfo>
            </Card>
            <Card>
                <h1></h1>
                {Object.entries(purchase).map((info, index) => {
                    console.log(info)
                    return (
                        <>
                            <StyleInfo><a href={info[1].food_link} target="_blank" rel="noreferrer">{info[1].food_name}</a> {info[1].food_count}</StyleInfo>
                        </>
                    )
                })}

            </Card>
        </Wrapper>
    )
}

export default DietUserBodyInfoCard;