import React from 'react';
import styled from "styled-components";

const Wrapper = styled.div`
    outline: 0px;
    display: flex;
    -webkit-tap-highlight-color: transparent;
    text-decoration: none;
    color: rgb(0, 0, 0);
    box-sizing: border-box;
    position: relative;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(8, 60, 130, 0.06) 0px 0px 0px 0.05rem, rgba(30, 34, 40, 0.04) 0rem 0rem 1.25rem;
    border-radius: 15px;
    padding: 24px;
    margin-top: 16px;
    &:hover {
        background-color: #8AE52E;
    }
`;

const FoodListCard = styled.div`
    margin-left: 16px;
    max-width: 300px;
    text-align: center;

`;

const Styleimg = styled.img`
    width: 100px;
    height: 100px;
`;

const FoodDataString = styled.div`
    margin-bottom: 4px;
`;
function FoodDataList(props) {
    const { foods } = props;
    return (
        <>
            {foods.map((food, index) => {
                return (
                    <Wrapper>
                        <Styleimg src={process.env.REACT_APP_API + food.image} />
                        <FoodListCard>
                            <FoodDataString> : {food.name}</FoodDataString>
                            <FoodDataString> : {food.kcal}kcal</FoodDataString>
                            <FoodDataString> : {food.protein}g</FoodDataString>
                            <FoodDataString> : {food.fat}g</FoodDataString>
                            <FoodDataString> : {food.carbs}g</FoodDataString>
                        </FoodListCard>
                    </Wrapper>
                )
            })}
        </>
    );
}

export default FoodDataList;