import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';

import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip } from 'recharts';
import { useNavigate } from 'react-router-dom';

import WeekDietLogList from '../../components/List/WeekDietLogList';

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  padding: 16px;

  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
`;

const ChartTitle = styled.h2`
  margin-bottom: 20px;
`;

const ChartWapper = styled.div`
  height: 500px;
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 32px;
`;

const ProfilePage = () => {
  const navigate = useNavigate();
  const [data, setData] = useState([]); //   
  const [weekdiets, setWeekdiets] = useState([]); //    
  
  const fetchProfile = async () => {
    try {
      const accessToken = localStorage.getItem('access_token');
      if (accessToken) {
        const response = await axios.get(
          `${process.env.REACT_APP_API}/api/userprofile/`,
          {
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
          }
        );
        const responseData = response.data;

        //   
        const updatedData = responseData.map((item) => ({
          name: item.created_at,
          Weight: item.weight,
        }));
        setData(updatedData);

        const weekdiets = responseData.map((item) => ({
          id: item.week_diet,
          title: item.title,
        }));

        setWeekdiets(weekdiets);
      } else {
        alert('  .');  
        localStorage.removeItem('access_token');
        navigate('/login');
      }
    } catch (error) {
      alert('      ');
      window.location.replace('/');
    }
  };

  useEffect(() => {
    fetchProfile();
  }, [data, weekdiets]); // data weekdiets   

  return (
    <Wrapper>
      {/*  */}
      <ChartWapper>
        <ChartTitle> </ChartTitle>
        {data.length > 0 ? ( //     
          <LineChart width={500} height={300} data={data}>
            <Line type="monotone" dataKey="Weight" stroke="#8884d8" />
            <CartesianGrid stroke="#ccc" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
          </LineChart>
        ) : (
          <p>Loading...</p> //      
        )}
      </ChartWapper>

      {/*   */}
      <ChartWapper>
        <h1> </h1>
        <WeekDietLogList weekdiets={weekdiets} />
      </ChartWapper>
    </Wrapper>
  );
};

export default ProfilePage;
